.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);    
  }

  .modal>.modal-main {
    position:fixed;
    background: #6b960f;
    border: 1px solid black;
    width: 40%;
    height: auto;
    top:50%;
    left:50%;
    color: black;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }